/* element */
body {
  position: relative;
  font-family: 'Noto Serif JP', serif;
  font-weight: 300;
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: #666;
  padding-top: 80px;
  /* background-color: #ccc; */
}
a {
  text-decoration: none;
  color: #666;
}

/* module */
.sp {
  display: none;
}
.heading2 {
  font-weight: 500;
  font-size: 32px;
  margin: 80px 0 100px;
  text-align: center;
}
.heading2 span {
  position: relative;
  display: inline-block;
  margin: 0 auto;
}
.heading2 span:after {
  bottom: -20px;
  left: 50%;
  content: '';
  position: absolute;
  width: 100%;
  max-width: calc(100% - 20px);
  height: 2px;
  background-color: #81c07a;
  transform: translateX(-50%);
}
.heading3 {
  font-weight: 500;
  font-size: 24px;
  margin: 20px 0 40px;
  text-align: center;
}
.heading3 span {
  position: relative;
  display: inline-block;
  margin: 0 auto;
}
.heading3 span:after {
  bottom: -10px;
  left: 50%;
  margin-left: -10px;
  content: '';
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: #81c07a;
}
.heading4 {
  font-weight: 500;
  font-size: 18px;
  margin: 16px 0;
  text-align: left;
}
.heading4 span {
  display: inline-block;
  margin: 0 auto;
}
/* grid */
.grid {
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: center;
  align-items: flex-start;
}
.grid .grid_item {
  box-sizing: border-box;
  flex: 0 0 45%;
  padding: 0;
}
.grid .grid_item:nth-child(2n) {
  margin-left: 5%;
}
/* link */
.link {
}
.link.link_right {
  padding-right: 25px;
  background: url(https://placehold.jp/20x20.png) right center no-repeat
}
.link.link_blank {
  padding-right: 25px;
  background: url(https://placehold.jp/20x20.png) right center no-repeat
}
.link.link_tel {
  padding-right: 25px;
  background: url(https://placehold.jp/20x20.png) right center no-repeat
}
/* linkList */
.linkList {

}
.linkList_item {

}
.linkList_item:not(:last-child) {
  margin-bottom: 20px;
}
/* btn */
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  height: 40px;
  margin: 0 auto;
  background: url(./assets/common/images/icon_blank_white.png) right 10px center / 20px no-repeat;
  background-color: #81c07a;
  border: 2px solid #fff;
  border-radius: 10px;
  transition: background-color .5s ease;
}
.btn .main {
  color: #fff;
}
.btn:hover {
  background-color: #fff;
  border: 2px solid #81c07a;
  background: url(./assets/common/images/icon_blank_green.png) right 10px center / 20px no-repeat;
  transition: background-color .5s ease;
}
.btn:hover .main{
  color: #81c07a;
}
.btnList {
  margin-bottom: 20px;
}
.btnList .btn:not(:last-child) {
  margin-bottom: 20px;
}

section.content {
  width: 100%;
  overflow: hidden;
}
section.content .content_outer {
  width: calc(100% - 80px);
  margin: 0 auto;
  /* background-color: green; */
}
/* mv */
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-wrapper {
  height: 100%;
}

.swiper-slide {
  height: 100%;
  background-position: center;
  background-size: cover;
}
.mv {
  position: relative;
  height: calc(100vh - 80px);
  z-index: 0;
}
.mv .mv_text {
  color: #fff;
  font-size: 24px;
}
.mv .mv_contents {
  display: flex;
  align-items: center;
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
}
.mv .mv_contents.pc-posi-1 {
  top: 30px;
  left: 100px;
  width: 500px;
  height: 80px;
}
.mv .mv_contents.pc-posi-2 {
  top: 30px;
  left: 50%;
  width: 500px;
  height: 80px;
  transform: translateX(-50%);
}
.mv .mv_contents.pc-posi-3 {
  top: 30px;
  right: 100;
  width: 500px;
  height: 80px;
}
.mv .mv_contents.pc-posi-4 {
  top: 50%;
  left: 100px;
  width: 500px;
  height: 80px;
  transform: translateY(-50%);
}
.mv .mv_contents.pc-posi-5 {
  top: 50%;
  left: 50%;
  width: 500px;
  height: 80px;
  transform: translateX(-50%);
  transform: translateY(-50%);
}
.mv .mv_contents.pc-posi-6 {
  top: 50%;
  right: 100px;
  width: 500px;
  height: 80px;
  transform: translateY(-50%);
}
.mv .mv_contents.pc-posi-7 {
  bottom: 30px;
  left: 100px;
  width: 500px;
  height: 80px;
}
.mv .mv_contents.pc-posi-8 {
  bottom: 30px;
  left: 50%;
  width: 500px;
  height: 80px;
  transform: translateX(-50%);
}
.mv .mv_contents.pc-posi-9 {
  bottom: 30px;
  right: 100px;
  width: 500px;
  height: 80px;
}

/* header */
header {
  position: fixed;
  top: 0;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  height: 80px;
  z-index: 99;
}
.header_logo {
  position: absolute;
  width: 200px;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}
.header_logo img {
  width: 40px;
  display: flex;
  align-items: center;
}

.header_nav {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
.header_nav__toggle {
  display: none;
}
.header_nav .header_nav__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.header_nav .header_nav__item {
  padding: 10px 20px;
  display: inline-block;
}

.header_contact {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.header_contact .header_contact__phone {
  display: flex;
  flex-direction: column;
}
.header_contact .header_contact__phone--text {
  font-size: 24px;
  font-weight: 600;
}
.header_contact .header_contact__phone--sub {
  font-size: 16px;
  align-self: flex-end;
}

/* footer */
footer {
  width: 100%;
  border-top: 1px solid #ccc;
}
.copyright {
  text-align: center;
  font-size: 14px;
  font-weight: 300;
}


/* ci */
section.ci .ci_content {
  max-width: 800px;
  margin: 0 auto;
}
.ci_content .ci_concept {
  font-size: 24px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
}
.ci_content .ci_main{
  text-align: center;
}
.ci_content .ci_main:not(:last-child) {
  margin-bottom: 10px;
}

/* achievement */
section.achievement .achievement_content {
  max-width: 1280px;
  margin: 0 auto;
}

.achievement_content .swiper-container.achievement {
  width: 800px;
}
.achievement_content .swiper-container.achievement .swiper-pagination.achievement_pagination {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  left: 0;
  z-index: 99;
}
.achievement_content .swiper-container.achievement .swiper-pagination.achievement_pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.achievement_content .swiper-container.achievement .swiper-pagination.achievement_pagination .achievement_bullet {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: 24px;
  border: 1px solid #666;
  background-color: #fff;
  border-radius: 8px;
  opacity: .5;
}
.achievement_content .swiper-container.achievement .swiper-pagination.achievement_pagination .achievement_bullet.swiper-pagination-bullet-active {
  background-color: #81c07a;
  border: none;
  color: #fff;
  opacity: 1;
}
.achievement_content .swiper-container.achievement .swiper-pagination.achievement_pagination .achievement_bullet:not(:last-child) {
  margin-right: 20px;
}
.achievement_content .achievement_table {
  width: 100%;
}
.achievement_content .achievement_table th,
.achievement_content .achievement_table td{
  padding: 10px;
}
.achievement_content .achievement_table td{
  font-family: "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", "Meiryo", "verdana", sans-serif;
  border: 1px solid #81c07a;
  text-align: right;
  width: 100px;
}
.achievement_content .achievement_table th{
  border-top: 1px solid #81c07a;
  border-bottom: 1px solid #81c07a;
  border-right: 1px solid #81c07a;
  border-left: 1px solid #fff;
}
.achievement_content .achievement_table tr:not(:last-child) th{
  border-bottom: 1px solid #fff;
}
.achievement_content .achievement_table th {
  min-width: 80px;
  background-color: #81c07a;
  font-weight: 300;
  color: #fff;
}

/* course */
section.course .course_content {
  max-width: 1280px;
  margin: 0 auto;
}

.course_content .course_table {
  width: 100%;
}
.course_content .course_table th,
.course_content .course_table td{
  padding: 10px;
}
.course_content .course_table td{
  border: 1px solid #81c07a;
}
.course_content .course_table th{
  border-top: 1px solid #81c07a;
  border-bottom: 1px solid #81c07a;
  border-right: 1px solid #81c07a;
  border-left: 1px solid #fff;
}
.course_content .course_table tr:not(:last-child) th{
  border-bottom: 1px solid #fff;
}
.course_content .course_table th {
  min-width: 80px;
  background-color: #81c07a;
  font-weight: 600;
  color: #fff;
}
.course_content .bby_logo a {
  display: block;
  padding: 20px;
  background-color: #17176C;
  height: 60px;
}
/* access */
section.access .access_content {
  max-width: 1280px;
  margin: 0 auto;
}
.access_content img {
  width: 100%;
}
/* contact */
section.contact .contact_content {
  max-width: 1280px;
  margin: 0 auto;
}
.contact_content .mail {
  font-size: 24px;
  letter-spacing: 0.05em;
  color: #81c07a;
}
.contact_content .mail:hover {
  text-decoration: underline;
}
.contact_content .tel {
  font-size: 30px;
  letter-spacing: 0.05em;
  color: #81c07a;
}
.contact_content .tel:hover {
  text-decoration: underline;
}
/* flow */
section.flow .flow_content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 80px 0;
}

.flow_list {
  display: flex;
  justify-content: space-around;
}
.flow_item {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 0 0 19%;
  max-width: 19%;
  align-items: center;
  justify-content: center;
  .flow_no {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #81c07a;
    color: #fff;
    margin-bottom: 20px;
  }
  .flow_title {
    font-size: 18px;
    font-weight: bold;
  }
  .flow_text {
    max-width: 100%;
  }
  &:not(:first-child):before {
    position: absolute;
    box-sizing: border-box;
    top: 50%;
    left: -25%;
    content: '';
    width: 80px;
    height: 80px;
    border-top: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 40px solid transparent;
    border-left: 40px solid #ddd;
    transform: translate(0, -50%);
  }
}
@media screen and (max-width: 980px) {
  .flow_item:not(:first-child):before {
    width: 40px;
    height: 40px;
    border-top: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-left: 20px solid #ddd;
  }
}
/* news */
section.news .news_content {
  margin: 0 auto;
  padding-bottom: 120px;
  text-align: center
}
.news_text {
  margin-bottom: 20px;
}
.fb-page {
  width: 500px;
}

