}

.header_nav {
  display: none;
  position: fixed;
  text-align: center;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #fff;
  transition: all .5s ease;
  transform: translateY(100%);
  .header_nav__inner {
    position: relative;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid #eee;
    .header_nav__toggle {
      position: absolute;
      content: '';
      top: -44px;
      left: 50%;
      width: 100px;
      height: 24px;
      font-size: 16px;
      transform: translateX(-50%);
      background-color: #fff;
      border: 1px solid #eee;
      border-bottom: none;
      border-radius: 8px 8px 0 0;
      padding-top: 20px;
      line-height: 1;
      &.hide:before {
        position: absolute;
        box-sizing: border-box;
        top: -2px;
        left: 50%;
        content: '';
        width: 18px;
        height: 18px;
        border-top: 9px solid transparent;
        border-right: 9px solid transparent;
        border-bottom: 9px solid #ddd;
        border-left: 9px solid transparent;
        transform: translate(-50%, 0);
      }
      &.show:before {
        position: absolute;
        box-sizing: border-box;
        top: 8px;
        left: 50%;
        content: '';
        width: 18px;
        height: 18px;
        border-top: 9px solid #ddd;
        border-right: 9px solid transparent;
        border-bottom: 9px solid transparent;
        border-left: 9px solid transparent;
        transform: translate(-50%, 0);
      }
    }
    .header_nav__item {
      flex: 1 1 80px;
      height: 30px;
      &.wide {
        flex: 1 1 auto;
      }
    }
  }
}

